import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Row from 'react-bootstrap/Row';
import GoogleMaps from '../GoogleMap/GoogleMaps';
import s from './Form.module.scss';

const Forma = () => {

  const handleCard = useSelector((state) => state.handleCard);
  const [validated, setValidated] = useState(false);
  const [form, setForm] = useState({});
  const [errors, setErrors] = useState({});

  const setField = (field, value) => {
    setForm({
      ...form,
      [field]: value
    });


    if (!!errors[field]) {
      setErrors({
        ...errors,
        [field]: null
      })
    }
    console.log(form);
    console.log(errors);
    console.log(handleCard);
  }

  const addDelivery = () => {
    setForm({
      ...form,
      'delivery': [...handleCard]
    });
  }

  const dispatch = useDispatch();

  const handleSubmit = (event) => {
    const formF = event.currentTarget;
    if (formF.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    axios({
      url: 'http://localhost:3001/save',
      method: 'POST',
      data: form
    })
      .then(() => { console.log('godgodgodgod eumy') })
      .catch(() => { console.log('errorrr') })
    setValidated(true);
  };

  return (
    <div className={s.groupForm}>
      <GoogleMaps />
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
        <Row className="mb-3">
          <Form.Group as={Col} md="6" controlId="validationCustom01">
            <Form.Label>First name</Form.Label>
            <Form.Control
              required
              type="text"
              placeholder="First name"
              defaultValue="Mark"
              value={form.name}
              onChange={(e) => setField('name', e.target.value)}
              isInvalid={!!errors.name}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="6" controlId="validationCustom02">
            <Form.Label>Last name</Form.Label>
            <Form.Control
              required
              type="text"
              placeholder="Last name"
              defaultValue="Otto"
              value={form.surName}
              onChange={(e) => setField('surName', e.target.value)}
              isInvalid={!!errors.surName}
            />
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Row className="mb-3">
          <Form.Group as={Col} md="8" controlId="formGridEmail">
            <Form.Label>Email</Form.Label>
            <InputGroup hasValidation>
              <InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>
              <Form.Control
                required
                type="email"
                placeholder="Enter email"
                value={form.email}
                onChange={(e) => setField('email', e.target.value)}
                isInvalid={!!errors.email}
              />
              <Form.Control.Feedback type="invalid">
                Please enter correct Email.
              </Form.Control.Feedback>
            </InputGroup>
            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Row className="mb-3">
          <Form.Group as={Col} md="6" controlId="validationCustom03">
            <Form.Label>City</Form.Label>
            <Form.Control
              type="text"
              placeholder="City"
              required
              value={form.city}
              onChange={(e) => setField('city', e.target.value)}
              isInvalid={!!errors.city}
            />
            <Form.Control.Feedback type="invalid">
              Please provide a valid city.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="3" controlId="validationCustom04">
            <Form.Label>State</Form.Label>
            <Form.Control
              type="text"
              placeholder="State"
              required
              value={form.state}
              onChange={(e) => setField('state', e.target.value)}
              isInvalid={!!errors.state}
            />
            <Form.Control.Feedback type="invalid">
              Please provide a valid state.
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group as={Col} md="3" controlId="validationCustom05">
            <Form.Label>Zip</Form.Label>
            <Form.Control
              type="text"
              placeholder="Zip"
              required
              value={form.zip}
              onChange={(e) => setField('zip', e.target.value)}
              isInvalid={!!errors.zip}
            />
            <Form.Control.Feedback type="invalid">
              Please provide a valid zip.
            </Form.Control.Feedback>
          </Form.Group>
        </Row>
        <Form.Group className="mb-3">
          <Form.Check
            required
            label="Agree to terms and conditions"
            feedback="You must agree before submitting."
            feedbackType="invalid"
            value={form.check}
            onChange={(e) => setField('check', e.target.value)}
            isInvalid={!!errors.check}
          />
        </Form.Group>
        <Button type="submit" onClick={() => addDelivery()}>Submit form</Button>
      </Form>
    </div>
  );
};

export default Forma;